import { createApolloFetch } from 'apollo-fetch';

export default class GraphQLClient {
  constructor({ uri, jwtToken, headers }) {
    this.apolloFetch = createApolloFetch({ uri });
    this.headers = headers;

    if (jwtToken) {
      this.setJwtToken(jwtToken);
    }
  }

  setJwtToken(jwtToken) {
    this.apolloFetch.use(({ options }, next) => {
      if (jwtToken) {
        // eslint-disable-next-line no-param-reassign
        options.headers = {
          ...options.headers,
          ...this.headers,
          authorization: `bearer ${jwtToken}`,
        };
      }

      next();
    });
  }

  async runQuery(query, variables) {
    const { data, errors } = await this.apolloFetch({ query, variables });
    if (errors) {
      throw errors;
    }
    return data;
  }

  // Convenience static method
  static async runQuery(options, query, variables) {
    return new GraphQLClient(options).runQuery(query, variables);
  }
}



