import { exec } from 'child_process';
import denodeify from 'denodeify';
import setupDebug from 'debug';

const debug = setupDebug('react-chromatic:tester:git');

async function execGitCommand(command) {
  try {
    return (await denodeify(exec)(command)).trim();
  } catch (error) {
    // eslint-disable-next-line no-console
    console.error(`Unable to execute git command '${command}'.\n`);
    if (error.message && error.message.match('Not a git repository')) {
      // eslint-disable-next-line no-console
      console.error(
        'Chromatic only works in git projects.\n' +
          'Contact us at support@hichroma.com if you need to use Chromatic outside of one.\n\n'
      );
    }
    throw error;
  }
}

export const FETCH_N_INITAL_BUILD_COMMITS = 20;
const TesterGetRecentBuildCommitsQuery = `
  query TesterGetRecentBuildCommitsQuery($newestCommittedAt: Float!) {
    app {
      buildCommits(limit: ${FETCH_N_INITAL_BUILD_COMMITS}, newestCommittedAt: $newestCommittedAt)
    }
  }
`;

const TesterGetAllPossibleBuildCommitsQuery = `
  query TesterGetAllPossibleBuildCommitsQuery($newestCommittedAt: Float!, $oldestCommittedAt: Float!) {
    app {
      buildCommits(skip: ${FETCH_N_INITAL_BUILD_COMMITS}, newestCommittedAt: $newestCommittedAt, oldestCommittedAt: $oldestCommittedAt)
    }
  }
`;

// NOTE: At some point we should check that the commit has been pushed to the
// remote and the branch matches with origin/REF, but for now we are naive about
// adhoc builds.

export async function getCommit() {
  const [commit, committedAtSeconds] = (await execGitCommand(`git log -n 1 --format="%H %ct"`))
    .trim()
    .split(' ');

  return { commit, committedAt: committedAtSeconds * 1000 };
}

export async function getBranch() {
  const branch = (await execGitCommand(`git rev-parse --abbrev-ref HEAD`)).trim();

  // On certain CI systems, a branch is not checked out (instead a detached head)
  // is used for the commit. We can use this environment variable in such cases:
  //   https://documentation.codeship.com/pro/builds-and-configuration/environment-variables/#default-environment-variables
  if (branch === 'HEAD') {
    return process.env.CI_BRANCH || branch;
  }
  return branch;
}

// We use rev-list to get all the commits that are ancestors of HEAD but not
// ancestors of any of the <commits>.
//
// These commits naturally form a tree that meets up to the complete history of
// <commits> (call that the "known" history, from chromatic's perspective).
// git calls the commits in the known history where the tree joins the "boundary".
// Of the boundary commits:
//   - Those that are actually members of <commits> correspond to builds that
//     we want to use as a baseline
//   - Otherwise they correspond to commits in the known history that we have
//     commit path to but no known build on that path.
//
// We are just going to follow a simple algorithm: on the first pass, grab
// X commits, check which are boundaries. If there are boundaries not in those
// commits, choose the *oldest*, and grab all builds that are more recent.
// In the second pass we do not care which
async function getBaselinesFromCommits(commits) {
  const boundaryData = await execGitCommand(
    `git rev-list HEAD --boundary --format='%m%H %ct' --ignore-missing \
      --not ${commits.map(c => c.trim()).join(' ')}`
  );

  const baselineCommits = [];
  let oldestCommittedAt = null;
  boundaryData
    .trim()
    .split('\n')
    // rev-list lists each commit like:
    // commit 4a1c922edd61fa0e9d3cb25d4e205816701557a5
    // >4a1c922edd61fa0e9d3cb25d4e205816701557a5 1495065352
    // We want the second line if it matches ("-")
    .filter(l => !l.match('commit') && l.match('-'))
    .forEach(rawRow => {
      const [commitWithDash, committedAtSeconds] = rawRow.trim().split(' ');
      const commit = commitWithDash.slice(1);

      if (commits.find(c => c === commit)) {
        baselineCommits.push(commit);
      } else if (oldestCommittedAt === null) {
        oldestCommittedAt = committedAtSeconds * 1000;
      } else {
        oldestCommittedAt = Math.min(oldestCommittedAt, committedAtSeconds * 1000);
      }
    });

  return {
    baselineCommits,
    oldestCommittedAt,
  };
}

// eslint-disable-next-line import/prefer-default-export
export async function getBaselineCommits(client) {
  // NOTE: the rev-list command above doesn't work in the case that the last
  // commit is exactly this commit. However this is easy for us to deal with!
  const { commit: currentCommit, committedAt } = await getCommit();

  // Get the (up to) X commits of the most recent builds of this app
  const recentCommits = (await client.runQuery(TesterGetRecentBuildCommitsQuery, {
    newestCommittedAt: committedAt,
  })).app.buildCommits;
  debug(`First ${FETCH_N_INITAL_BUILD_COMMITS} commits: ${recentCommits}`);

  // Short-circuit: on first run, there's definitely no baseline!
  if (recentCommits.length === 0) {
    return [];
  }

  if (recentCommits.find(c => c === currentCommit)) {
    return [currentCommit];
  }

  const {
    baselineCommits: recentBaselineCommits,
    oldestCommittedAt,
  } = await getBaselinesFromCommits(recentCommits);
  debug(`Baselines from initial commits: ${recentBaselineCommits} [${oldestCommittedAt}]`);

  // console.log(recentBaselineCommits, oldestCommittedAt);
  // Important optimization. If we are sure that there aren't any older relevant
  // builds, we can avoid an extra query
  if (oldestCommittedAt === null || recentCommits.length < FETCH_N_INITAL_BUILD_COMMITS) {
    return recentBaselineCommits;
  }

  // We aren't sure we know everything, so run a (potentially) more expensive query
  // to get *all* builds that are newer than oldestCommittedAt
  const allPossibleCommits = (await client.runQuery(TesterGetAllPossibleBuildCommitsQuery, {
    newestCommittedAt: committedAt,
    oldestCommittedAt,
  })).app.buildCommits;
  debug(`allPossibleCommits: ${allPossibleCommits}`);

  const { baselineCommits } = await getBaselinesFromCommits([
    ...recentCommits,
    ...allPossibleCommits,
  ]);
  debug(`allPossible baselineCommits: ${baselineCommits}`);
  return baselineCommits;
}



