import { spawn } from 'child_process';
import fetch from 'isomorphic-fetch';

const CHECK_EVERY = 1000;
const TIMEOUT = 5 * 60 * 1000;

export async function checkResponse(url) {
  try {
    await fetch(url);
    return true;
  } catch (e) {
    return false;
  }
}

async function waitForResponse(child, url) {
  const timeoutAt = Date.now() + TIMEOUT;
  return new Promise((resolve, reject) => {
    let resolved = false;
    async function check() {
      if (Date.now() > timeoutAt) {
        resolved = true;
        reject(new Error(`No server responding at ${url} within ${TIMEOUT / 1000} seconds.`));
        return;
      }

      if (await checkResponse(url)) {
        resolved = true;
        resolve();
        return;
      }
      setTimeout(check, CHECK_EVERY);
    }
    check();

    if (child) {
      let output = '';
      child.stderr.on('data', e => {
        output += e.toString();
      });
      child.stdout.on('data', o => {
        output += o.toString();
      });

      child.on('close', () => {
        if (!resolved) {
          reject(new Error(`Script failed to start: ${output}\n`));
        }
      });
    }
  });
}

export default async function startApp({ scriptName = 'start', url }) {
  let child;
  if (scriptName !== 'none') {
    if (await checkResponse(url)) {
      throw new Error(
        `Detected process already running at ${url}` +
          `\nIf you are sure this is your server, pass \`--do-not-start\` to skip this step.`
      );
    }

    child = spawn('npm', ['run', scriptName], {
      env: {
        ...process.env,
        NODE_ENV: 'development',
        BROWSER: 'none',
      },
    });
  }

  await waitForResponse(child, url);
  return child;
}



